<template>
  <div class="guidepage">
    <!-- 如果用户第一次体验此应用，先进入引导页在进入跳转页 -->
    <!-- 进入过引导页，则默认进入广告页  再首页-->
    <!-- http://localhost:8080/#/guidepage 引导页 -->
    <!-- http://localhost:8080/#/advertpage 广告页 -->
    <div class="swiper-container guidepage-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <img src="../../assets/yindao01.png" alt />
        </div>
        <div class="swiper-slide">
          <img src="../../assets/yindao02.png" alt />
        </div>
        <div class="swiper-slide">
          <img src="../../assets/yindao03.png" alt />
          <div class="btn" @click="jump"></div>
        </div>
      </div>
      <!-- Add Pagination -->
      <div class="swiper-pagination guidepage-pagination"></div>
    </div>
  </div>
</template>

<script>
import Swiper from "swiper";
export default {
  methods:{
    jump(){
      this.$router.push({name:"homepage"})
    }
  },
  created() {
    //隐藏导航
    this.$store.state.vantTab = false;
  },

  mounted() {
    new Swiper(".guidepage-container", {
       pagination: {
        el: '.guidepage-pagination',
      },
    });
  }
};
</script>

<style lang="less" scoped>
.guidepage {
  width: 100%;
  height: 100%;
  .swiper-container {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    .swiper-slide {
      text-align: center;
      img {
        height: 100%;
      }
    }
  }
  .btn{
    z-index: 1;
    position: absolute;
    bottom: 41px;
    left: 100px;
    width: 190px;
    height: 53px;
    background-color: skyblue;
    border-radius: 50px;
    opacity:0.1;
  }
}
</style>